* {
  margin: 0px 0px;
}

div,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: white;
}

a {
  text-decoration: none;
}

.App {
  padding: 0vh 15vw;

  // background-color: black;
}

body {
  background: #0f0f13;
}


@media only screen and (min-width : 1650px) {
  .App {
    padding: 0vh 20vw;
  }
}

::-webkit-scrollbar {
  width: 10px;
  height: 0px;

}

::-webkit-scrollbar-track {
  background: #16161d;

}


::-webkit-scrollbar-thumb {
  background: rgb(64, 63, 63);

}


::-webkit-scrollbar-thumb:hover {
  background: rgb(51, 50, 50);

}