// Primary container
.Projects {
    // padding-top: 10vh;
    padding: 1cm 0cm 2cm 0cm;

    min-height: 88vh;

}

.tech {
    color: rgb(149, 149, 149);
    // color: rgb(147, 147, 147);

    font-size: 12.5px;
    padding-top: .2cm;

}

.tech-light {
    color: rgb(50, 50, 50);

}

.tech-icon {
    width: .5cm;
}

// projects container
.projects-container {
    padding: .3cm 0vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: .5cm;
    row-gap: .5cm;
}

// project box
.project {

    // border: 1px solid #b97ce2;

    border: 1px solid rgb(64, 64, 64);

    background-color: #0f0f13;
    backdrop-filter: blur(10px);




    width: 7.5cm;
    height: auto;
    padding: 0.5cm;








    border-radius: 25px;

    box-shadow: rgba(0, 0, 0, 0.500) 0px 2px 4px 0px, rgba(0, 0, 0, 0.500) 0px 4px 12px 4px;


    &:hover {
        border: 1px solid #b97ce2;
    }




}

// .project:hover{

//     box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(120, 119, 119) 0px 0px 0px 1px;


// }
.project-desc {
    padding-top: .3cm;
    font-size: 16px;
    color: rgb(220, 220, 220);
}

// project logo and name container 
.project-logo-container {
    display: flex;
    align-items: center;
    column-gap: .3cm;
}

.project-year {
    // padding-left: 1.6cm;
    padding-bottom: .1cm;
    color: rgb(0, 182, 0);
    font-size: 12px;
    text-align: right;
    // cursor: pointer;

}

// .project-year:hover{
// text-decoration: underline;
// }

.project-logo {
    width: 1cm;
}

.project-name {
    font-size: 20px;

    color: rgba(255, 255, 255, 0.92);


    font-family: fira sans;
    // font-weight: 600;

}

.project-name:hover {
    text-decoration: underline;
}

.project-tagline {
    font-size: 14px;
    // letter-spacing: .02cm;
    color: rgb(152, 152, 152);
    font-family: roboto;
}

// container of button redirect to source code of project
.code {
    display: flex;
    align-items: baseline;
    column-gap: .08cm;
    width: 100%;
    border: .5px solid rgb(83, 83, 83);
    width: fit-content;

    border-radius: 100px;
    cursor: pointer;




}

.project-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: .2cm;
}

.code:hover {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    background-color: rgb(230, 65, 87);
    border: thin solid #0f0f13;

}

// primary container of buttons
.project-buttons {
    // margin-left: 2cm;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: .3cm;
    flex-wrap: wrap;
    row-gap: .4cm;

}

.header-project {
    display: flex;
    column-gap: .1cm;
    justify-content: flex-end;
    align-items: baseline;
    cursor: pointer;
}

.header-project:hover {
    text-decoration: underline;
    text-decoration-color: #FFC107;
    text-decoration-thickness: 1.5px;
}

.starss {
    color: #FFC107;
}

.star {
    width: .25cm;
    height: .25cm;
}

// button redirect to source code of project
.code-link {
    // color:rgb(160, 160, 160);
    color: rgba(255, 255, 255, 0.922);
    text-decoration: none;
    width: 100%;
    cursor: pointer;
    font-size: 15px;
    padding: .25cm .8cm;
    border-radius: 100px;
    font-size: 18px;
    // font-weight: 600;


    .code-link:hover {
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background-color: rgb(230, 65, 87);
        border: thin solid #0f0f13;

    }
}


// buttons redirect icon
.redirect {
    width: .23cm;
}


// light mode code
.project-light {
    background-color: transparent;


    // border: 1px solid rgba(24, 24, 27, 0.08);
    color: rgba(16, 185, 129, 0.08);
    box-shadow: rgba(60, 64, 67, 0.3) 0px 2px 4px 0px, rgba(60, 64, 67, 0.15) 0px 4px 12px 4px;

}

.project-name-light {
    color: black;
    letter-spacing: 0cm;
}

.project-tagline-light {
    color: rgb(68, 68, 68);

}

.project-desc-light {
    color: rgb(35, 35, 35);

}

.project-year-light {
    color: rgb(119, 0, 231);
}

.code-link-light {
    color: black;
    cursor: pointer;
    font-size: 13px;
    // font-weight: 500;
    padding: .05cm .05cm;

    font-weight: 600;


    .code-link-light:hover {
        text-decoration: underline;

    }
}

.code-light {

    border: none;
    width: fit-content;
    border: thin rgb(176, 176, 176) solid;
    border-radius: 3px;
    cursor: pointer;
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;


    // background-color: #16161d;
    // background-color: #2c2b2b;

    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;



}

.code-light:hover {
    box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1.2px, rgb(97, 96, 96) 0px 0px 0px 1.2px;

}




@media only screen and (max-width : 1450px) {
    .App {
        padding: 0vh 5vw;
    }
}

@media screen and (max-width:1100px) {


    .project {
        width: 6.6cm;
    }

    .projects-container {
        padding: .1cm 0vh;
    }

}

@media screen and (max-width:900px) {


    .project {
        width: 6.7cm;
    }

    .projects-container {
        padding: .1cm 0vh;
    }

}

@media screen and (max-width:800px) {
    .project {
        width: 6cm;
    }


}

@media screen and (max-width:700px) {
    .project {
        width: 70vw;
    }



    .projects-container {
        padding: .2cm 0vh;
    }
}

@media screen and (max-width:600px) {

    .project {
        width: 90vw;
    }

    .project-dsc {
        font-size: 15px;
    }

    .tech {
        font-size: 12px;
    }

}

@media screen and (max-width:400px) {
    .project {
        width: 90vw;
    }

    .project-logo {
        width: .9cm;
    }



}

@media screen and (max-width:300px) {

    .project {
        padding: .3cm;
    }

}