// primary container
.Skills {
    padding: 1cm 0cm 2cm 0cm;

    min-height: 88vh;

}

// skills container
.skills-container {
    padding-top: .7cm;
    display: flex;
    justify-content: center;
    column-gap: .6cm;
    row-gap: .6cm;
    flex-wrap: wrap;
}

// skill card
.skill {
    
    // border: 1px solid rgba(255, 255, 255, 0.1);
    background-color: #0f0f13;
    width: 3.3cm;
    padding: .6cm .3cm;
    height: fit-content;
    // background-color: #242425;






    border-radius: 25px;

   


    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    // border: 1px solid rgb(240, 106, 124);
    border: 1px solid rgb(64, 64, 64);
    box-shadow: rgba(0, 0, 0, 0.500) 0px 2px 4px 0px, rgba(0, 0, 0, 0.500) 0px 4px 12px 4px;

        &:hover{
            border: 1px solid rgb(240, 106, 124);
        }
}

