// image of skill
.skill-img {
    width: 1.5cm;
    padding: .1cm;
    transition: .1s;
}

// skill name
.skill-name {
    font-size: 16px;
    padding-top: .2cm;
    letter-spacing: .025cm;
    // color: rgba(197, 193, 193, 0.951);
    color: rgba(255, 255, 255,);

    font-family: raleway;
    font-weight: 500;
}

// skill proficiency
.proficiency {
    color: blueviolet;
    font-size: 18px;
}

.skill-light {
    // background-color: rgb(240, 239, 239);
    background-color: rgb(245, 245, 245);


    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;


    border: none;

}

.skill-name-light {
    color: black;
}

@media screen and (max-width:1000px) {
    .skill-name {
        font-size: 16px;
    }

    .skill-img {
        width: 1.4cm;

    }
}

@media screen and (max-width:700px) {
    .skill-name {
        font-size: 16px;
    }

    .skill {
        width: 3cm;
    }

    .skill-img {
        width: 1.2cm;

    }
}